    </div>

    <!-- 🔽 篩選條件區塊 -->
    <el-collapse v-show="showFilter">
      <el-card class="filter-panel">
        <el-form label-width="180px">
          <!-- Load Type -->
          <el-row>
            <el-col :span="12">
              <el-form-item label="Load Type">
                <el-select
                  v-model="filter.loadType"
                  placeholder="Select Load Type"
                  clearable
                  filterable
                >
                  <el-option label="Import" value="I" />
                  <el-option label="Domestic" value="D" />
                  <el-option label="Export" value="E" />
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <!-- Schedule Date -->
          <el-row>
            <el-col :span="16">
              <el-form-item label="SCHEDULE DATE">
                <el-date-picker
                  v-model="filter.scheduleDate"
                  type="daterange"
                  format="MM-DD-YYYY"
                  value-format="YYYY-MM-DD"
                  range-separator="To"
                  start-placeholder="Start"
                  end-placeholder="End"
                />
              </el-form-item>
            </el-col>
          </el-row>

          <!-- ERD -->
          <el-row>
            <el-col :span="16">
              <el-form-item label="ERD">
                <el-date-picker
                  v-model="filter.erd"
                  type="daterange"
                  format="MM-DD-YYYY"
                  value-format="YYYY-MM-DD"
                  range-separator="To"
                  start-placeholder="Start"
                  end-placeholder="End"
                />
              </el-form-item>
            </el-col>
          </el-row>

          <!-- CUT OFF -->
          <el-row>
            <el-col :span="16">
              <el-form-item label="CUT OFF">
                <el-date-picker
                  v-model="filter.cutOff"
                  type="daterange"
                  format="MM-DD-YYYY"
                  value-format="YYYY-MM-DD"
                  range-separator="To"
                  start-placeholder="Start"
                  end-placeholder="End"
                />
              </el-form-item>
            </el-col>
          </el-row>

          <!-- DOC CUT -->
          <el-row>
            <el-col :span="16">
              <el-form-item label="DOC CUT">
                <el-date-picker
                  v-model="filter.docCut"
                  type="daterange"
                  format="MM-DD-YYYY"
                  value-format="YYYY-MM-DD"
                  range-separator="To"
                  start-placeholder="Start"
                  end-placeholder="End"
                />
              </el-form-item>
            </el-col>
          </el-row>

          <!-- Load ID, Driver User ID, Load Status -->
          <el-row :gutter="10">
            <el-col :span="8">
              <el-form-item label="Load ID">
                <el-input v-model="filter.loadId" placeholder="Enter Load ID" />
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="Driver User ID">
                <el-select
                  v-model="filter.driverUserId"
                  placeholder="Select Driver"
                  clearable
                  filterable
                >
                  <el-option
                    v-for="d in drivers"
                    :key="d.driver_user_id"
                    :label="d.driver_name"
                    :value="d.driver_user_id"
                  />
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="Load Status">
                <el-select
                  v-model="filter.loadStatus"
                  placeholder="Select Status"
                  clearable
                  filterable
                >
                  <el-option
                    v-for="status in loadStatusOptions"
                    :key="status.value"
                    :label="status.label"
                    :value="status.value"
                  />
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <!-- Booking, Container, Customer/Location -->
          <el-row :gutter="10">
            <el-col :span="8">
              <el-form-item label="Booking">
                <el-input
                  v-model="filter.booking"
                  placeholder="Enter Booking"
                />
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="CONTAINER #">
                <el-input
                  v-model="filter.container"
                  placeholder="Enter Container #"
                />
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="CUSTOMER NAME">
                <el-select
                  v-model="filter.customerName"
                  placeholder="Customer Name"
                  clearable
                  filterable
                >
                  <el-option
                    v-for="cust in customers"
                    :key="cust.customer_id"
                    :label="cust.customer_name"
                    :value="cust.customer_id"
                  />
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <!-- ADDRESS DETAIL, SSL, Loads Notes -->
          <el-row :gutter="10">
            <el-col :span="8">
              <el-form-item label="CUSTOMER / LOCATION">
                <el-select
                  v-model="filter.customerLocation"
                  placeholder="Select Customer/Location"
                  clearable
                  filterable
                >
                  <el-option
                    v-for="loc in locations"
                    :key="loc.location_id"
                    :label="loc.customer_location"
                    :value="loc.location_id"
                  />
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="ADDRESS DETAIL">
                <el-input
                  v-model="filter.addressDetail"
                  placeholder="Enter Address Detail"
                />
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="SSL">
                <el-select
                  v-model="filter.ssl"
                  placeholder="Select SSL"
                  clearable
                >
                  <el-option
                    v-for="ssl in sslOptions"
                    :key="ssl.id"
                    :label="ssl.name"
                    :value="ssl.id"
                  />
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <!-- SCALE/HAZ, Load Number/PO, Size -->
          <el-row :gutter="10">
            <el-col :span="8">
              <el-form-item label="Loads Notes">
                <el-input
                  v-model="filter.loadNotes"
                  placeholder="Enter Loads Notes"
                />
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="SCALE / HAZ">
                <el-select
                  v-model="filter.scaleHaz"
                  placeholder="Select Option"
                  clearable
                  filterable
                >
                  <el-option
                    v-for="s in scaleHazmatOptions"
                    :key="s.value"
                    :label="s.label"
                    :value="s.value"
                  />
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="Load Number / PO">
                <el-input
                  v-model="filter.loadNumberPo"
                  placeholder="Enter Load Number / PO"
                />
              </el-form-item>
            </el-col>
          </el-row>

          <!-- PICK TERMINAL, DROP TERMINAL, VESSEL -->
          <el-row :gutter="10">
            <el-col :span="8">
              <el-form-item label="Size">
                <el-input v-model="filter.size" placeholder="Enter Size" />
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="PICK TERMINAL">
                <el-select
                  v-model="filter.pickTerminal"
                  placeholder="Pick Terminal"
                  clearable
                  filterable
                >
                  <el-option
                    v-for="pier in piers"
                    :key="pier.pier_id"
                    :label="pier.pier_name"
                    :value="pier.pier_id"
                  />
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="DROP TERMINAL">
                <el-select
                  v-model="filter.dropTerminal"
                  placeholder="Drop Terminal"
                  clearable
                  filterable
                >
                  <el-option
                    v-for="pier in piers"
                    :key="pier.pier_id"
                    :label="pier.pier_name"
                    :value="pier.pier_id"
                  />
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="8">
              <el-form-item label="VESSEL">
                <el-input v-model="filter.vessel" placeholder="Enter Vessel" />
              </el-form-item>
            </el-col>
          </el-row>

          <!-- 🔹 按鈕區塊 -->
          <el-row justify="end">
            <el-col :span="6" class="filter-buttons">
              <el-form-item>
                <el-button type="primary" @click="searchOrders"
                  >Search</el-button
                >
                <el-button type="warning" @click="clearFilters"
                  >Clear</el-button
                >
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-card>
    </el-collapse>

    <!-- 📌 分頁功能（上方） -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="filteredOrders.length"
      :page-size="pageSize"
      v-model:current-page="currentPage"
      class="pagination-container top-pagination"
    />

    <!-- 🔵 Add / Cancel / Batch Edit 按鈕 -->
    <div class="button-container">
      <el-button type="primary" size="small" @click="openAddDialog">
        Add
      </el-button>
      <el-button type="danger" size="small" @click="cancelOrder">
        Cancel Order
      </el-button>
      <el-button type="warning" size="small" @click="openBatchEdit">
        Batch Edit
      </el-button>
    </div>

    <!-- 🚛 訂單表格 -->
    <div class="table-container">
      <el-table
        ref="orderTable"
        :data="orderList.length ? orderList : []"
        stripe
        border
        @selection-change="handleSelectionChange"
        @scroll="syncScroll"
        style="width: 100%; min-width: 3750px"
      >
        <!-- ✅ 勾選欄位 -->
        <el-table-column type="selection" width="55" />

        <!-- ✅ 操作欄位 (取消) -->
        <el-table-column label="Actions" width="100">
          <template #default="{ row }">
            <el-button type="primary" size="small" @click="handleEdit(row)"
              >Edit</el-button
            >
          </template>
        </el-table-column>

        <!-- ✅ 主要欄位 -->
        <el-table-column
          label="Load Type"
          width="150"
          :formatter="formatOrderType"
        />
        <el-table-column
          prop="order.str_schedule_date"
          label="SCHEDULE DATE"
          width="180"
        />
        <el-table-column label="Load ID" width="150">
          <template #default="{ row }">
            <span v-if="row.order.must_do" class="must-do-icon">❗</span>
            {{ row.order.order_id }}
          </template>
        </el-table-column>
        <el-table-column
          prop="detail.driver_user_id"
          label="Driver User ID"
          width="180"
        />
        <el-table-column
          prop="detail.str_order_status"
          label="Load Status"
          width="200"
        />
        <el-table-column prop="order.str_erd" label="ERD" width="150" />
        <el-table-column prop="order.str_cut_off" label="CUT OFF" width="150" />
        <el-table-column prop="order.booking_id" label="Booking" width="180" />
        <el-table-column
          prop="order.str_doc_date"
          label="DOC CUT"
          width="200"
        />
        <el-table-column
          prop="detail.container"
          label="CONTAINER #"
          width="180"
        />
        <el-table-column
          prop="order.str_customer"
          label="CUSTOMER / LOCATION"
          width="250"
        />
        <el-table-column
          prop="order.customer_address"
          label="ADDRESS DETAIL"
          width="300"
        />
        <el-table-column prop="order.str_ssl" label="SSL" width="150" />
        <el-table-column label="Loads Notes" width="250">
          <template #default="{ row }">
            <div
              class="load-notes-cell"
              v-html="row.order.load_notes.replace(/\n/g, '<br>')"
            ></div>
          </template>
        </el-table-column>
        <el-table-column
          prop="order.scale_hazmat"
          label="SCALE / HAZ"
          width="150"
        />
        <el-table-column
          prop="order.po_no"
          label="Load Number / PO"
          width="180"
        />
        <el-table-column prop="order.container_size" label="Size" width="120" />
        <el-table-column
          prop="order.pick_terminal"
          label="PICK TERMINAL"
          width="150"
        />
        <el-table-column
          prop="order.drop_terminal"
          label="DROP TERMINAL"
          width="150"
        />
        <el-table-column prop="order.vessel" label="VESSEL" width="200" />
      </el-table>
    </div>

    <!-- 📌 分頁功能（下方） -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="filteredOrders.length"
      :page-size="pageSize"
      v-model:current-page="currentPage"
      class="pagination-container bottom-pagination"
    />

    <!-- 📝 新增對話框 -->
    <OrderAddDialog
      :visible="addDialogVisible"
      @close="closeAddDialog"
      @add-success="handleAddSuccess"
    />

    <!-- 📝 編輯對話框 -->
    <OrderEditDialog
      :visible="editDialogVisible"
      :orderData="selectedOrder"
      @update:visible="editDialogVisible = $event"
      @update-success="yourRefreshMethod"
    />

    <!-- 📝 批量修改對話框 -->
    <OrderBatchEditDialog
      :visible="batchEditDialogVisible"
      :selectedOrders="selectedOrders"
      @update:visible="batchEditDialogVisible = $event"
      @batch-update-success="fetchOrders"
    />
  </div>
</template>

<script>
import axios from "axios";
import { mapState, mapActions } from "vuex";
import { API_BASE_URL } from "@/config";
import dayjs from "dayjs";
import isoWeek from "dayjs/plugin/isoWeek";
import OrderAddDialog from "./OrderAddDialog.vue";
import OrderEditDialog from "./OrderEditDialog.vue";
import OrderBatchEditDialog from "./OrderBatchEditDialog.vue";

dayjs.extend(isoWeek);

export default {
  components: {
    OrderAddDialog,
    OrderEditDialog,
    OrderBatchEditDialog,
  },

  setup() {
    const formatOrderType = (row) => {
      const typeMap = {
        I: "Import",
        D: "Domestic",
        E: "Export",
      };
      return typeMap[row.order?.order_type] || "Unknown";
    };

    return {
      formatOrderType,
    };
  },

  data() {
    return {
      addDialogVisible: false,
      editDialogVisible: false,
      batchEditDialogVisible: false,
      selectedOrder: null,
      orderList: [],
      selectedOrders: [],
      searchQuery: "",
      loading: false,
      currentPage: 1,
      pageSize: 100,
      showFilter: false,
      filter: {
        scheduleDate: null,
        erd: null,
        cutOff: null,
        docCut: null,
        loadType: "",
        loadId: "",
        driverUserId: "",
        loadStatus: "",
        booking: "",
        container: "",
        customerLocation: "",
        addressDetail: "",
        loadNotes: "",
        loadNumberPo: "",
        pickTerminal: "",
        dropTerminal: "",
        vessel: "",
        customerName: "",
        ssl: "",
        scaleHaz: "",
        size: "",
      },
      drivers: [],
      customers: [],
      piers: [],
      locations: [],
      scaleHazmatOptions: [
        { label: "None", value: 0 },
        { label: "ScaleTicket", value: 1 },
        { label: "Hazmat", value: 2 },
        { label: "ScaleTicketAndHazmat", value: 3 },
      ],
      sslOptions: [
        { id: 1, name: "ACL" },
        { id: 2, name: "CMA/CGM" },
        { id: 3, name: "COSCO" },
        { id: 4, name: "ECLK" },
        { id: 5, name: "EVERGREEN" },
        { id: 6, name: "HAMBURG SUD" },
        { id: 7, name: "HAPAG" },
        { id: 8, name: "HMM" },
        { id: 9, name: "MAERSK" },
        { id: 10, name: "MSC" },
        { id: 11, name: "ONE" },
        { id: 12, name: "OOCL" },
        { id: 13, name: "SEALAND" },
        { id: 14, name: "TRKU" },
        { id: 15, name: "WANHAI" },
        { id: 16, name: "YANGMING" },
        { id: 17, name: "ZIM" },
        { id: 18, name: "To be decided" },
      ],
      loadStatusOptions: [
        { label: "Waiting For Vessel Date", value: 0 },
        { label: "Waiting For Driver To Confirm", value: 1 },
        { label: "Need Dispatch", value: 2 },
        { label: "Picking Up", value: 3 },
        { label: "On The Way To Loador/Unload", value: 4 },
        { label: "Loading Or Unloading", value: 5 },
        { label: "Dropping Off", value: 6 },
        { label: "Waiting For Validation", value: 7 },
        { label: "Cancelled", value: 8 },
        { label: "Dry Run", value: 9 },
        { label: "Complete", value: 10 },
        { label: "Needre Dispatch", value: 11 },
        { label: "Rejected Files", value: 12 },
        { label: "Void Out Ticket", value: 13 },
        { label: "Void Out Only", value: 14 },
      ],
    };
  },

  computed: {
    ...mapState(["authToken"]),

    filteredOrders() {
      if (!this.searchQuery) return this.orderList;
      const query = this.searchQuery.toLowerCase();
      return this.orderList.filter(
        (order) =>
          order.order.customer_id_name?.toLowerCase().includes(query) ||
          order.order.order_id?.toString().includes(query)
      );
    },

    paginatedOrders() {
      const start = (this.currentPage - 1) * this.pageSize;
      return this.filteredOrders.slice(start, start + this.pageSize);
    },
  },

  methods: {
    ...mapActions(["checkTokenExpiration"]),

    clearFilters() {
      Object.keys(this.filter).forEach((key) => {
        this.filter[key] = Array.isArray(this.filter[key]) ? null : "";
      });
      this.searchQuery = "";
      this.locations = [];
      this.filter.addressDetail = "";
    },

    async fetchCustomerLocations() {
      if (!this.filter.customerName) {
        this.locations = [];
        this.filter.customerLocation = "";
        this.filter.addressDetail = "";
        return;
      }

      const token = localStorage.getItem("authToken");
      try {
        const response = await axios.post(`${API_BASE_URL}/Customer/Location`, {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          customer_id: this.filter.customerName,
        });
        if (response.data.Code === 1000) {
          this.locations = response.data.location_list;
        } else {
          this.locations = [];
        }

        // 每次重選Customer後都清空原有的location與address
        this.filter.customerLocation = "";
        this.filter.addressDetail = "";
      } catch (error) {
        console.error("Error fetching locations:", error);
        this.locations = [];
        this.filter.customerLocation = "";
        this.filter.addressDetail = "";
      }
    },

    updateCustomerAddress() {
      const selectedLocation = this.locations.find(
        (loc) => loc.location_id === this.filter.customerLocation
      );

      this.filter.addressDetail = selectedLocation
        ? selectedLocation.customer_address ||
          selectedLocation.address_detail ||
          selectedLocation.address ||
          ""
        : "";
    },

    async handleEdit(order) {
  const token = localStorage.getItem("authToken");
  try {
    const response = await axios.post(`${API_BASE_URL}/Order/List`, {
      Token: token,
      Timestamp: Date.now(),
      Sign: "string",
      order_id: String(order.order.order_id), // 正確的 order_id
      year: 0,
      week: 0,
    });

    if (
      response.data.Code === 1000 &&
      response.data.order_list.length > 0
    ) {
      const { order: orderMain, detail } = response.data.order_list[0];

      const orderDetail = {
        ...orderMain,
        ...detail,
        arrival_photo: detail.arrival_photo
          ? JSON.parse(detail.arrival_photo)
          : [],
        departure_photo: detail.departure_photo
          ? JSON.parse(detail.departure_photo)
          : [],
        bol_photo: detail.bol_path ? JSON.parse(detail.bol_path) : [],
        pickup_tir: detail.tir_pu_empty_path
          ? JSON.parse(detail.tir_pu_empty_path)
          : [],
        dropoff_tir: detail.tir_drop_path
          ? JSON.parse(detail.tir_drop_path)
          : [],
        void_out_ticket: detail.void_ticket_path
          ? JSON.parse(detail.void_ticket_path)
          : [],
      };

      console.log("完整的 Order 資料:", orderDetail);

      this.selectedOrder = orderDetail;
      this.editDialogVisible = true;
    } else {
      alert("Failed to fetch order details.");
    }
  } catch (error) {
    console.error("Error fetching order detail:", error);
    alert("An error occurred. Please try again later.");
  }
},


  yourRefreshMethod() {
    this.fetchOrders();
  },

    openAddDialog() {
      this.addDialogVisible = true;
    },

    closeAddDialog() {
      this.addDialogVisible = false;
    },

    handleAddSuccess() {
      this.fetchOrders();
      this.closeAddDialog();
    },

    openBatchEdit() {
      if (!this.selectedOrders.length) {
        alert("Please select at least one order.");
        return;
      }
      this.batchEditDialogVisible = true;
    },

    async searchOrders() {
  this.loading = true;
  const token = localStorage.getItem("authToken");
  if (!token) {
    alert("Session expired, please log in again.");
    this.$router.push("/login");
    return;
  }

  try {
    // 構造 data payload，移除空值欄位
    const filterData = {
      schedule_date_start: this.filter.scheduleDate?.[0],
      schedule_date_end: this.filter.scheduleDate?.[1],
      erd_start: this.filter.erd?.[0],
      erd_end: this.filter.erd?.[1],
      cut_off_start: this.filter.cutOff?.[0],
      cut_off_end: this.filter.cutOff?.[1],
      doc_cut_start: this.filter.docCut?.[0],
      doc_cut_end: this.filter.docCut?.[1],
      order_type: this.filter.loadType,
      load_id: this.filter.loadId,
      driver_user_id: this.filter.driverUserId,
      order_status: this.filter.loadStatus,
      booking: this.filter.booking,
      container: this.filter.container,
      customer_id: this.filter.customerName,
      customer_location: this.filter.customerLocation,
      address_detail: this.filter.addressDetail,
      load_notes: this.filter.loadNotes,
      po_no: this.filter.loadNumberPo,
      pick_terminal: this.filter.pickTerminal,
      drop_terminal: this.filter.dropTerminal,
      vessel: this.filter.vessel,
      ssl: this.filter.ssl,
      scale_hazmat: this.filter.scaleHaz,
      container_size: this.filter.size,
    };

    // 過濾掉值為 `null` 或 `""` 的屬性
    const cleanedData = Object.fromEntries(
      Object.entries(filterData).filter(([, value]) => value)
    );

    // API Payload
    const payload = {
      Token: token,
      Timestamp: Date.now(),
      Sign: "string",
      data: cleanedData, // 只發送有值的條件
    };

    console.log("🔹 發送 API payload:", JSON.stringify(payload, null, 2));

    // 發送 API 請求
    const response = await axios.post(
      `${API_BASE_URL}/Order/SearchV2`,
      payload
    );

    // 若成功回應，更新 orderList
    if (response.data.Code === 1000) {
      this.orderList = response.data.order_list;
      this.currentPage = 1; // 重新設置分頁

      // ✅ Show success feedback
      this.$message.success("Search completed successfully!");
    } else {
      this.orderList = [];
      alert("No results found.");
    }
  } catch (error) {
    console.error("❌ 搜索 API 請求失敗:", error);
    alert("An error occurred. Please try again later.");
  } finally {
    this.loading = false;
  }
},


    async fetchOrders() {
      this.loading = true;
      const token = localStorage.getItem("authToken");
      if (!token) {
        alert("Session expired, please log in again.");
        this.$router.push("/login");
        return;
      }

      const startOfWeek = dayjs().startOf("isoWeek").format("YYYY-MM-DD");
      const endOfWeek = dayjs().endOf("isoWeek").format("YYYY-MM-DD");

      try {
        const response = await axios.post(`${API_BASE_URL}/Order/ListByDate`, {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          start_date: startOfWeek,
          end_date: endOfWeek,
        });
        this.orderList =
          response.data.Code === 1000 ? response.data.order_list : [];
      } catch (error) {
        console.error("Error fetching orders:", error);
        this.orderList = [];
      } finally {
        this.loading = false;
      }
    },

    handleSelectionChange(selectedRows) {
      this.selectedOrders = selectedRows.map((row) => row.order.order_id);
    },

    async cancelOrder() {
      if (!this.selectedOrders.length) {
        alert("Please select orders to cancel first!");
        return;
      }

      try {
        const token = localStorage.getItem("authToken");
        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          orders: this.selectedOrders,
        };
        const response = await axios.post(
          `${API_BASE_URL}/Order/Cancel`,
          payload
        );

        if (response.data.Code === 1000) {
          alert("Order(s) canceled successfully!");
          this.fetchOrders();
        } else {
          alert("Failed to cancel orders.");
        }
      } catch (error) {
        console.error("Cancel API Error:", error);
      }
    },

    async fetchDrivers() {
      const response = await axios.post(
        `${API_BASE_URL}/Truck/TruckDriverList`,
        {
          Token: this.authToken,
          Timestamp: Date.now(),
          Sign: "string",
          truck_id: "",
        }
      );
      this.drivers = response.data.driver_list || [];
    },

    async fetchCustomers() {
      const response = await axios.post(`${API_BASE_URL}/Customer/List`, {
        Token: this.authToken,
        Timestamp: Date.now(),
        Sign: "string",
      });
      this.customers = response.data.customer_list || [];
    },

    async fetchPiers() {
      const response = await axios.post(`${API_BASE_URL}/Customer/Pier`, {
        Token: this.authToken,
        Timestamp: Date.now(),
        Sign: "string",
        pier_id: 0,
      });
      this.piers = response.data.pier_list || [];
    },
  },

  watch: {
    "filter.customerName"() {
      this.filter.customerLocation = "";
      this.filter.addressDetail = "";
      this.fetchCustomerLocations();
    },
    "filter.customerLocation"(newVal) {
      if (newVal) {
        this.updateCustomerAddress();
      } else {
        this.filter.addressDetail = "";
      }
    },
  },

  mounted() {
    this.fetchOrders();
    this.fetchDrivers();
    this.fetchCustomers();
    this.fetchPiers();
  },
};
</script>

