<template>
  <el-dialog
    v-model="dialogVisible"
    title="Batch Edit Orders"
    width="600px"
    @close="closeDialog"
  >
    <el-form :model="batchForm" label-width="180px">
      <el-form-item label="Booking">
        <el-input v-model="batchForm.booking_id" />
      </el-form-item>

      <el-form-item label="CONTAINER #">
        <el-input v-model="batchForm.container" />
      </el-form-item>

      <el-form-item label="PICK TERMINAL">
        <el-select v-model="batchForm.pick_terminal" clearable>
          <el-option
            v-for="pier in piers"
            :key="pier.pier_id"
            :label="pier.pier_name"
            :value="pier.pier_id"
          />
        </el-select>
      </el-form-item>

      <el-form-item label="Pick Up Date">
        <el-date-picker
          v-model="batchForm.pick_up_date"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DDTHH:mm:ss.SSS"
        />
      </el-form-item>

      <el-form-item label="DROP TERMINAL">
        <el-select v-model="batchForm.drop_terminal" clearable>
          <el-option
            v-for="pier in piers"
            :key="pier.pier_id"
            :label="pier.pier_name"
            :value="pier.pier_id"
          />
        </el-select>
      </el-form-item>

      <el-form-item label="Drop Off Date">
        <el-date-picker
          v-model="batchForm.drop_off_date"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DDTHH:mm:ss.SSS"
        />
      </el-form-item>

      <el-form-item label="SCALE / HAZ">
        <el-select v-model="batchForm.scale_hazmat" clearable>
          <el-option
            v-for="option in scaleHazmatOptions"
            :key="option.value"
            :label="option.label"
            :value="option.value"
          />
        </el-select>
      </el-form-item>

      <el-form-item label="ERD">
        <el-date-picker
          v-model="batchForm.erd"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DDTHH:mm:ss.SSS"
        />
      </el-form-item>

      <el-form-item label="CUT OFF">
        <el-date-picker
          v-model="batchForm.cut_off"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DDTHH:mm:ss.SSS"
        />
      </el-form-item>

      <el-form-item label="SSL">
        <el-select v-model="batchForm.ssl_id" clearable>
          <el-option
            v-for="option in sslOptions"
            :key="option.id"
            :label="option.name"
            :value="option.id"
          />
        </el-select>
      </el-form-item>

      <el-form-item label="SCHEDULE DATE">
        <el-date-picker
          v-model="batchForm.schedule_date"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DDTHH:mm:ss.SSS"
        />
      </el-form-item>

      <el-form-item label="Loads Notes">
        <el-input v-model="batchForm.load_notes" type="textarea" rows="3" />
      </el-form-item>

      <el-form-item label="Size">
        <el-input v-model="batchForm.container_size" />
      </el-form-item>

      <el-form-item label="VESSEL">
        <el-input v-model="batchForm.vessel" />
      </el-form-item>

      <el-form-item label="Load Number / PO">
        <el-input v-model="batchForm.po_no" />
      </el-form-item>

      <el-form-item label="Reference Number">
        <el-input v-model="batchForm.Reference_Number" />
      </el-form-item>

      <el-form-item label="Paperwork Validation">
        <el-checkbox v-model="batchForm.paperwork_confirmation"></el-checkbox>
      </el-form-item>

      <el-form-item label="Load Status">
        <el-select v-model="batchForm.order_status" clearable>
          <el-option
            v-for="status in loadStatusOptions"
            :key="status.value"
            :label="status.label"
            :value="status.value"
          />
        </el-select>
      </el-form-item>

      <el-form-item label="Driver User ID">
        <el-select v-model="batchForm.driver_user_id" clearable filterable>
          <el-option
            v-for="driver in drivers"
            :key="driver.driver_user_id"
            :label="driver.driver_name"
            :value="driver.driver_user_id"
          />
        </el-select>
      </el-form-item>

      <el-form-item label="DOC CUT">
        <el-date-picker
          v-model="batchForm.doc_cut"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DDTHH:mm:ss.SSS"
        />
      </el-form-item>
    </el-form>

    <template #footer>
      <el-button @click="closeDialog">Cancel</el-button>
      <el-button type="primary" @click="submitBatchEdit">Save</el-button>
    </template>
  </el-dialog>
</template>

<script>
import { ref, watch, onMounted } from "vue";
import axios from "axios";
import { API_BASE_URL } from "@/config";

export default {
  props: {
    visible: Boolean,
    selectedOrders: Array,
  },
  emits: ["update:visible", "batch-update-success"],
  setup(props, { emit }) {
    const dialogVisible = ref(props.visible);

    watch(
      () => props.visible,
      (val) => {
        dialogVisible.value = val;
      }
    );

    const batchForm = ref({});

    // 🔸 從API抓取的資料
    const sslOptions = ref([]);
    const scaleHazmatOptions = ref([
      { label: "None", value: 0 },
      { label: "ScaleTicket", value: 1 },
      { label: "Hazmat", value: 2 },
      { label: "ScaleTicketAndHazmat", value: 3 },
    ]);

    const loadStatusOptions = ref([
      { label: "Waiting For Vessel Date", value: "0" },
      { label: "Waiting For Driver To Confirm", value: "1" },
      { label: "Need Dispatch", value: "2" },
      { label: "Picking Up", value: "3" },
      { label: "On The Way To Loador/Unload", value: "4" },
      { label: "Loading Or Unloading", value: "5" },
      { label: "Dropping Off", value: "6" },
      { label: "Waiting For Validation", value: "7" },
      { label: "Cancelled", value: "8" },
      { label: "Dry Run", value: "9" },
      { label: "Complete", value: "10" },
      { label: "Needre Dispatch", value: "11" },
      { label: "Rejected Files", value: "12" },
      { label: "Void Out Ticket", value: "13" },
      { label: "Void Out Only", value: "14" },
    ]);

    const drivers = ref([]);
    const piers = ref([]);

    const fetchDrivers = async () => {
      const token = localStorage.getItem("authToken");
      const res = await axios.post(`${API_BASE_URL}/Truck/TruckDriverList`, {
        Token: token,
        Timestamp: Date.now(),
        Sign: "string",
        truck_id: "",
      });
      if (res.data.Code === 1000) {
        drivers.value = res.data.driver_list;
      }
    };

    const fetchPiers = async () => {
      const token = localStorage.getItem("authToken");
      const res = await axios.post(`${API_BASE_URL}/Customer/Pier`, {
        Token: token,
        Timestamp: Date.now(),
        Sign: "string",
        pier_id: 0,
      });
      if (res.data.Code === 1000) {
        piers.value = res.data.pier_list;
      }
    };

    const fetchSSL = () => {
      sslOptions.value = [
        { id: 1, name: "ACL" },
        { id: 2, name: "CMA/CGM" },
        { id: 3, name: "COSCO" },
        { id: 4, name: "ECLK" },
        { id: 5, name: "EVERGREEN" },
        { id: 6, name: "HAMBURG SUD" },
        { id: 7, name: "HAPAG" },
        { id: 8, name: "HMM" },
        { id: 9, name: "MAERSK" },
        { id: 10, name: "MSC" },
        { id: 11, name: "ONE" },
        { id: 12, name: "OOCL" },
        { id: 13, name: "SEALAND" },
        { id: 14, name: "TRKU" },
        { id: 15, name: "WANHAI" },
        { id: 16, name: "YANGMING" },
        { id: 17, name: "ZIM" },
        { id: 18, name: "To be decided" },
      ];
    };

    onMounted(() => {
      fetchDrivers();
      fetchPiers();
      fetchSSL();
    });

    const fields = [
      { label: "Booking", prop: "booking_id", type: "input" },
      { label: "CONTAINER #", prop: "container", type: "input" },
      {
        label: "PICK TERMINAL",
        prop: "pick_terminal",
        type: "select",
        options: piers,
      },
      { label: "Pick Up Date", prop: "pick_up_date", type: "date" },
      {
        label: "DROP TERMINAL",
        prop: "drop_terminal",
        type: "select",
        options: piers,
      },
      { label: "Drop Off Date", prop: "drop_off_date", type: "date" },
      {
        label: "SCALE / HAZ",
        prop: "scale_hazmat",
        type: "select",
        options: scaleHazmatOptions,
      },
      { label: "ERD", prop: "erd", type: "date" },
      { label: "CUT OFF", prop: "cut_off", type: "date" },
      { label: "SSL", prop: "ssl_id", type: "select", options: sslOptions },
      { label: "SCHEDULE DATE", prop: "schedule_date", type: "date" },
      { label: "Loads Notes", prop: "load_notes", type: "input" },
      { label: "Size", prop: "container_size", type: "input" },
      { label: "VESSEL", prop: "vessel", type: "input" },
      { label: "Load Number / PO", prop: "po_no", type: "input" },
      { label: "Reference Number", prop: "Reference_Number", type: "input" },
      {
        label: "Paperwork Validation",
        prop: "paperwork_confirmation",
        type: "checkbox",
      },
      {
        label: "Load Status",
        prop: "order_status",
        type: "select",
        options: loadStatusOptions,
      },
      {
        label: "Driver User ID",
        prop: "driver_user_id",
        type: "select",
        options: drivers,
      },
      { label: "DOC CUT", prop: "doc_cut", type: "date" },
    ];

    const submitBatchEdit = async () => {
      const token = localStorage.getItem("authToken");
      const dataToSend = {};
      Object.entries(batchForm.value).forEach(([key, val]) => {
        if (val !== undefined && val !== null && val !== "") {
          dataToSend[key] = val;
        }
      });

      const payload = {
        Token: token,
        Timestamp: Date.now(),
        Sign: "string",
        orders: props.selectedOrders,
        data: dataToSend,
      };

      console.log("🚀 送出的資料：", JSON.stringify(payload, null, 2));

      try {
        const response = await axios.post(
          `${API_BASE_URL}/Order/EditList`,
          payload
        );

        if (response.data.Code === 1000) {
          emit("batch-update-success");
          closeDialog();
        } else {
          alert("Batch Edit failed");
          console.log("⚠️ API 回應錯誤：", response.data);
        }
      } catch (error) {
        console.error("❌ Batch Edit Error:", error);
        alert("Batch Edit Error");
      }
    };

    const closeDialog = () => emit("update:visible", false);

    return {
      dialogVisible,
      batchForm,
      fields,
      submitBatchEdit,
      closeDialog,
      piers,
      sslOptions,
      loadStatusOptions,
      scaleHazmatOptions,
      drivers,
    };
  },
};
</script>
