<script>
import axios from "axios";
import { API_BASE_URL } from "@/config";
import dayjs from "dayjs";
import isoWeek from "dayjs/plugin/isoWeek";

dayjs.extend(isoWeek);

export default {
  data() {
    return {
      billingList: [],
      totalCustomer: 0,
      totalDriver: 0,
      totalProfit: 0,
      currentPage: 1,
      pageSize: 50,
    };
  },

  computed: {
    paginatedBillingList() {
      const start = (this.currentPage - 1) * this.pageSize;
      return this.billingList.slice(start, start + this.pageSize);
    },
  },

  methods: {
    async fetchBillingData() {
      const token = localStorage.getItem("authToken");
      const start_date = dayjs().startOf("isoWeek").format("YYYY-MM-DD");
      const end_date = dayjs().endOf("isoWeek").format("YYYY-MM-DD");

      try {
        const response = await axios.post(`${API_BASE_URL}/Billing/List`, {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          start_date,
          end_date,
        });

        if (response.data.Code === 1000) {
          this.billingList = response.data.data;
          this.totalCustomer = response.data.Customer;
          this.totalDriver = response.data.Driver;
          this.totalProfit = response.data.Profit;
        } else {
          this.billingList = [];
        }
      } catch (error) {
        console.error("Error fetching billing data:", error);
        this.billingList = [];
      }
    },
  },

  mounted() {
    this.fetchBillingData();
  },
};
</script>

