<template>
  <el-dialog
    v-model="dialogVisible"
    title="Order Add"
    width="800px"
    @close="closeDialog"
  >
    <template v-if="step === 1">
      <el-form :model="orderData" label-width="140px">
        <!-- Must Do Checkbox -->
        <el-form-item label="MustDo">
          <el-checkbox v-model="orderData.must_do"></el-checkbox>
        </el-form-item>

        <!-- Load Type -->
        <el-form-item label="Load Type">
          <el-select v-model="orderData.order_type">
            <el-option label="Import" value="I" />
            <el-option label="Domestic" value="D" />
            <el-option label="Export" value="E" />
          </el-select>
        </el-form-item>

        <!-- Customer Name -->
        <el-form-item label="Customer Name">
          <el-select
            v-model="orderData.customer_id"
            filterable
            placeholder="Select Customer"
            @change="fetchCustomerLocations"
          >
            <el-option
              v-for="c in customers"
              :key="c.customer_id"
              :label="c.customer_name"
              :value="c.customer_id"
            />
          </el-select>
        </el-form-item>

        <!-- Customer / Location -->
        <el-form-item label="Customer / Location">
          <el-select
            v-model="orderData.customer_location"
            filterable
            placeholder="Select Location"
            @change="updateCustomerAddress"
          >
            <el-option
              v-for="loc in locations"
              :key="loc.location_id"
              :label="loc.customer_location"
              :value="loc.location_id"
            />
          </el-select>
        </el-form-item>

        <!-- Address Detail -->
        <el-form-item label="Address Detail">
          <el-input v-model="orderData.customer_address" />
        </el-form-item>

        <!-- Delivery Address -->
        <el-form-item label="Delivery Address">
          <el-input v-model="orderData.delivery_address" />
        </el-form-item>

        <!-- Booking -->
        <el-form-item label="Booking">
          <el-input v-model="orderData.booking_id" />
        </el-form-item>

        <!-- ERD -->
        <el-form-item label="ERD">
          <el-date-picker
            v-model="orderData.erd"
            type="date"
            format="MM-DD-YYYY"
            value-format="YYYY-MM-DDTHH:mm:ss.SSS"
          />
        </el-form-item>

        <!-- CUT OFF -->
        <el-form-item label="CUT OFF">
          <el-date-picker
            v-model="orderData.cut_off"
            type="date"
            format="MM-DD-YYYY"
            value-format="YYYY-MM-DDTHH:mm:ss.SSS"
          />
        </el-form-item>

        <!-- SCHEDULE DATE -->
        <el-form-item label="SCHEDULE DATE">
          <el-date-picker
            v-model="orderData.schedule_date"
            type="date"
            format="MM-DD-YYYY"
            value-format="YYYY-MM-DDTHH:mm:ss.SSS"
          />
        </el-form-item>

        <!-- PICK TERMINAL -->
        <el-form-item label="PICK TERMINAL">
          <el-select
            v-model="orderData.pick_terminal"
            placeholder="Select Pick Terminal"
          >
            <el-option
              v-for="pier in piers"
              :key="pier.pier_id"
              :label="pier.pier_name"
              :value="pier.pier_id"
            />
          </el-select>
        </el-form-item>

        <!-- DROP TERMINAL -->
        <el-form-item label="DROP TERMINAL">
          <el-select
            v-model="orderData.drop_terminal"
            placeholder="Select Drop Terminal"
          >
            <el-option
              v-for="pier in piers"
              :key="pier.pier_id"
              :label="pier.pier_name"
              :value="pier.pier_id"
            />
          </el-select>
        </el-form-item>

        <!-- SCALE / HAZ -->
        <el-form-item label="SCALE / HAZ">
          <el-select
            v-model="orderData.scale_hazmat"
            placeholder="Select Scale / Haz"
          >
            <el-option
              v-for="option in scaleHazmatOptions"
              :key="option.value"
              :label="option.label"
              :value="option.value"
            />
          </el-select>
        </el-form-item>

        <!-- SSL -->
        <el-form-item label="SSL">
          <el-select v-model="orderData.ssl_id" placeholder="Select SSL">
            <el-option
              v-for="option in sslOptions"
              :key="option.id"
              :label="option.name"
              :value="option.id"
            />
          </el-select>
        </el-form-item>

        <!-- Loads Notes (多行) -->
        <el-form-item label="Loads Notes">
          <el-input v-model="orderData.load_notes" type="textarea" :rows="3" />
        </el-form-item>

        <!-- Size -->
        <el-form-item label="Size">
          <el-input v-model="orderData.container_size" />
        </el-form-item>

        <!-- Vessel -->
        <el-form-item label="Vessel">
          <el-input v-model="orderData.vessel" />
        </el-form-item>

        <!-- Load Number / PO -->
        <el-form-item label="Load Number / PO">
          <el-input v-model="orderData.po_no" />
        </el-form-item>

        <!-- Reference Number -->
        <el-form-item label="Reference Number">
          <el-input v-model="orderData.reference_number" />
        </el-form-item>

        <!-- DOC CUT -->
        <el-form-item label="DOC CUT">
          <el-date-picker v-model="orderData.doc_cut" type="datetime" />
        </el-form-item>
      </el-form>
    </template>

    <template v-if="step === 2">
      <el-form label-width="200px">
        <el-form-item label="Count">
          <el-input v-model.number="addOrderCount" type="number" />
        </el-form-item>
        <el-form-item label="Different PO">
          <el-select v-model="differentPO">
            <el-option label="Yes" value="Y" />
            <el-option label="No" value="N" />
          </el-select>
        </el-form-item>
        <el-form-item label="Different Schedule date">
          <el-select v-model="differentScheduleDate">
            <el-option label="Yes" value="Y" />
            <el-option label="No" value="N" />
          </el-select>
        </el-form-item>
      </el-form>
    </template>

    <template v-if="step === 3">
      <el-table :data="poNumbers" border>
        <el-table-column label="PO">
          <template #default="{ row }">
            <el-input v-model="row.po" />
          </template>
        </el-table-column>
        <el-table-column label="Schedule Date">
          <template #default="{ row }">
            <el-date-picker v-model="row.sdate" type="date" />
          </template>
        </el-table-column>
      </el-table>
      <el-button type="primary" @click="addPOEntry">Add PO</el-button>
    </template>

    <template #footer>
      <el-button @click="closeDialog">Close</el-button>
      <el-button v-if="step > 1" @click="step--">Back</el-button>
      <el-button type="primary" v-if="step < 3" @click="step++">Next</el-button>
      <el-button
        type="success"
        v-if="
          step === 3 ||
          (step === 2 && differentPO === 'N' && differentScheduleDate === 'N')
        "
        @click="submitOrder"
        >Save</el-button
      >
    </template>
  </el-dialog>
</template>

<script>
import { ref, watch, onMounted } from "vue";
import axios from "axios";
import { API_BASE_URL } from "@/config";

export default {
  props: {
    visible: Boolean,
  },
  emits: ["close", "add-success"],
  setup(props, { emit }) {
    const step = ref(1);
    const dialogVisible = ref(props.visible);
    const customers = ref([]); // 客戶列表
    const locations = ref([]); // 位置列表
    const piers = ref([]); // 碼頭列表
    const addOrderCount = ref(1);
    const differentPO = ref("N");
    const differentScheduleDate = ref("N");
    const poNumbers = ref([]);

    const scaleHazmatOptions = ref([
      { label: "None", value: 0 },
      { label: "ScaleTicket", value: 1 },
      { label: "Hazmat", value: 2 },
      { label: "ScaleTicketAndHazmat", value: 3 },
    ]);

    const sslOptions = ref([
      { id: 1, name: "ACL" },
      { id: 2, name: "CMA/CGM" },
      { id: 3, name: "COSCO" },
      { id: 4, name: "ECLK" },
      { id: 5, name: "EVERGREEN" },
      { id: 6, name: "HAMBURG SUD" },
      { id: 7, name: "HAPAG" },
      { id: 8, name: "HMM" },
      { id: 9, name: "MAERSK" },
      { id: 10, name: "MSC" },
      { id: 11, name: "ONE" },
      { id: 12, name: "OOCL" },
      { id: 13, name: "SEALAND" },
      { id: 14, name: "TRKU" },
      { id: 15, name: "WANHAI" },
      { id: 16, name: "YANGMING" },
      { id: 17, name: "ZIM" },
      { id: 18, name: "To be decided" },
    ]);

    watch(
      () => props.visible,
      (newVal) => {
        dialogVisible.value = newVal;
      }
    );

    const orderData = ref({}); // 先定義空的，之後用 resetOrderData() 設定

    const resetOrderData = () => {
      orderData.value = {
        order_id: 0, // ✅ 固定為 0
        order_type: "I",
        customer_id: null,
        customer_location: "",
        customer_address: "",
        delivery_address: "",
        booking_id: "",
        erd: "",
        cut_off: "",
        schedule_date: "",
        pick_terminal: "", // 選中的 pick 碼頭 pier_id
        drop_terminal: "", // 選中的 drop 碼頭 pier_id
        scale_hazmat: 0, // 預設為 None
        ssl_id: 18, // 預設 SSL 為 To be decided
        load_notes: "",
        container_size: "",
        vessel: "",
        po_no: "",
        reference_number: "",
        doc_cut: "",
        collection: 0,
        payment: 0,
        overtime_price: 0,
        overtime_payment: 0,
        load_year: 0,
        load_week: 0,
        paperwork_confirmation: false,
        must_do: false,
      };
    };

    // 讀取所有客戶列表
    const fetchCustomers = async () => {
      try {
        const token = localStorage.getItem("authToken");
        const response = await axios.post(`${API_BASE_URL}/Customer/List`, {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
        });
        if (response.data.Code === 1000) {
          customers.value = response.data.customer_list;
        }
      } catch (error) {
        console.error("Error fetching customers:", error);
      }
    };

    // 讀取所有碼頭列表（Piers）
    const fetchPiers = async () => {
      try {
        const token = localStorage.getItem("authToken");
        const response = await axios.post(`${API_BASE_URL}/Customer/Pier`, {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          pier_id: 0, // 取得所有碼頭
        });
        if (response.data.Code === 1000) {
          piers.value = response.data.pier_list;
        }
      } catch (error) {
        console.error("Error fetching piers:", error);
      }
    };

    // 當選擇 Customer Name 時，獲取對應的 Location
    const fetchCustomerLocations = async () => {
      if (!orderData.value.customer_id) return;
      try {
        const token = localStorage.getItem("authToken");
        const response = await axios.post(`${API_BASE_URL}/Customer/Location`, {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          customer_id: orderData.value.customer_id,
        });
        if (response.data.Code === 1000) {
          locations.value = response.data.location_list;
        }
      } catch (error) {
        console.error("Error fetching locations:", error);
      }
    };

    // 當 Customer / Location 變更時，更新 Address Detail
    const updateCustomerAddress = () => {
      const selectedLocation = locations.value.find(
        (loc) => loc.location_id === orderData.value.customer_location
      );
      orderData.value.customer_address = selectedLocation
        ? selectedLocation.customer_address || ""
        : "";
    };

    onMounted(() => {
      fetchCustomers();
      fetchPiers(); // 載入所有碼頭
      resetOrderData(); // 🔹 確保初始化時 orderData 乾淨
    });

    const addPOEntry = () => {
      poNumbers.value.push({ po: "", sdate: "" });
    };

    const submitOrder = async () => {
      const token = localStorage.getItem("authToken");

      const payload = {
        Token: token,
        Timestamp: Date.now(),
        Sign: "string",
        order_data: {
          ...orderData.value,
          must_do: orderData.value.must_do ? true : false,
        },
        add_order_count: addOrderCount.value,
      };

      if (differentPO.value === "Y" || differentScheduleDate.value === "Y") {
        payload.po_numbers = poNumbers.value;
        await axios.post(`${API_BASE_URL}/Order/AddByDfPoSdate`, payload);
      } else {
        await axios.post(`${API_BASE_URL}/Order/Add`, payload);
      }

      emit("add-success");
      closeDialog();
    };

    const closeDialog = () => {
      resetOrderData(); // 🔹 關閉時清空輸入的資料
      step.value = 1;
      emit("close");
    };

    return {
      step,
      dialogVisible,
      orderData,
      addOrderCount,
      differentPO,
      differentScheduleDate,
      poNumbers,
      customers,
      locations,
      piers, // ✅ 加入碼頭列表
      scaleHazmatOptions, // ✅ 回傳 scaleHazmatOptions 給模版使用
      sslOptions, //✅ 回傳 sslOptions 給模版使用
      addPOEntry,
      submitOrder,
      closeDialog,
      fetchCustomerLocations,
      updateCustomerAddress,
    };
  },
};
</script>
