<template>
  <el-dialog
    v-model="dialogVisible"
    title="Edit Order"
    width="800px"
    @close="closeDialog"
  >
    <el-form :model="orderData" label-width="180px">
      <!-- Load Type -->
      <el-form-item label="Load Type">
        <el-select v-model="orderForm.order_type">
          <el-option label="Import" value="I" />
          <el-option label="Domestic" value="D" />
          <el-option label="Export" value="E" />
        </el-select>
      </el-form-item>

      <!-- Customer Name -->
      <el-form-item label="Customer Name">
        <el-select
          v-model="orderForm.customer_id"
          filterable
          @change="fetchCustomerLocations"
        >
          <el-option
            v-for="c in customers"
            :key="c.customer_id"
            :label="c.customer_name"
            :value="c.customer_id"
          />
        </el-select>
      </el-form-item>

      <!-- Customer / Location -->
      <el-form-item label="CUSTOMER / Location">
        <el-select
          v-model="orderForm.customer_location"
          filterable
          @change="updateCustomerAddress"
        >
          <el-option
            v-for="loc in locations"
            :key="loc.location_id"
            :label="loc.customer_location"
            :value="loc.location_id"
          />
        </el-select>
      </el-form-item>

      <!-- ADDRESS DETAIL -->
      <el-form-item label="ADDRESS DETAIL">
        <el-input v-model="orderForm.customer_address" />
      </el-form-item>

      <!-- Booking -->
      <el-form-item label="Booking">
        <el-input v-model="orderForm.booking_id" />
      </el-form-item>

      <!-- CONTAINER # -->
      <el-form-item label="CONTAINER #">
        <el-input v-model="orderForm.container" />
      </el-form-item>

      <!-- PICK TERMINAL -->
      <el-form-item label="PICK TERMINAL">
        <el-select v-model="orderForm.pick_terminal">
          <el-option
            v-for="pier in piers"
            :key="pier.pier_id"
            :label="pier.pier_name"
            :value="pier.pier_id"
          />
        </el-select>
      </el-form-item>

      <!-- Pick Up Date -->
      <el-form-item label="Pick Up Date">
        <el-date-picker
          v-model="orderForm.pick_up_date"
          type="date"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DDTHH:mm:ss.SSS"
        />
      </el-form-item>

      <!-- DROP TERMINAL -->
      <el-form-item label="DROP TERMINAL">
        <el-select v-model="orderForm.drop_terminal">
          <el-option
            v-for="pier in piers"
            :key="pier.pier_id"
            :label="pier.pier_name"
            :value="pier.pier_id"
          />
        </el-select>
      </el-form-item>

      <!-- Drop Off Date -->
      <el-form-item label="Drop Off Date">
        <el-date-picker
          v-model="orderForm.drop_off_date"
          type="date"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DDTHH:mm:ss.SSS"
        />
      </el-form-item>

      <!-- SCALE / HAZ -->
      <el-form-item label="SCALE / HAZ">
        <el-select v-model="orderForm.scale_hazmat">
          <el-option
            v-for="option in scaleHazmatOptions"
            :key="option.value"
            :label="option.label"
            :value="option.value"
          />
        </el-select>
      </el-form-item>

      <!-- ERD -->
      <el-form-item label="ERD">
        <el-date-picker
          v-model="orderForm.erd"
          type="date"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DDTHH:mm:ss.SSS"
        />
      </el-form-item>

      <!-- CUT OFF -->
      <el-form-item label="CUT OFF">
        <el-date-picker
          v-model="orderForm.cut_off"
          type="date"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DDTHH:mm:ss.SSS"
        />
      </el-form-item>

      <!-- SSL -->
      <el-form-item label="SSL">
        <el-select v-model="orderForm.ssl_id">
          <el-option
            v-for="option in sslOptions"
            :key="option.id"
            :label="option.name"
            :value="option.id"
          />
        </el-select>
      </el-form-item>

      <!-- SCHEDULE DATE -->
      <el-form-item label="SCHEDULE DATE">
        <el-date-picker
          v-model="orderForm.schedule_date"
          type="date"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DDTHH:mm:ss.SSS"
        />
      </el-form-item>

      <!-- Loads Notes -->
      <el-form-item label="Loads Notes">
        <el-input v-model="orderForm.load_notes" type="textarea" rows="3" />
      </el-form-item>

      <!-- Size -->
      <el-form-item label="Size">
        <el-input v-model="orderForm.container_size" />
      </el-form-item>

      <!-- VESSEL -->
      <el-form-item label="VESSEL">
        <el-input v-model="orderForm.vessel" />
      </el-form-item>

      <!-- Load Number / PO -->
      <el-form-item label="Load Number / PO">
        <el-input v-model="orderForm.po_no" />
      </el-form-item>

      <!-- Reference Number -->
      <el-form-item label="Reference Number">
        <el-input v-model="orderForm.Reference_Number" />
      </el-form-item>

      <!-- Paperwork Validation -->
      <el-form-item label="Paperwork Validation">
        <el-checkbox v-model="orderForm.paperwork_confirmation"></el-checkbox>
      </el-form-item>

      <!-- Load Status -->
      <el-form-item label="Load Status">
        <el-select v-model="orderForm.order_status">
          <el-option
            v-for="status in loadStatusOptions"
            :key="status.value"
            :label="status.label"
            :value="status.value"
          />
        </el-select>
      </el-form-item>

      <!-- Driver User ID -->
      <el-form-item label="Driver User ID">
        <el-select v-model="orderForm.driver_user_id" filterable>
          <el-option
            v-for="driver in drivers"
            :key="driver.driver_user_id"
            :label="driver.driver_name"
            :value="driver.driver_user_id"
          />
        </el-select>
      </el-form-item>

      <!-- Arrival Time -->
      <el-form-item label="Arrival Time">
        <el-date-picker v-model="orderForm.arrival_time" type="datetime" />
      </el-form-item>

      <!-- Arrival Photo (圖片) -->
      <el-form-item label="Arrival Photo">
        <img
          v-if="orderForm.arrival_photo && orderForm.arrival_photo.length"
          :src="orderForm.arrival_photo[0]"
          style="max-width: 400px; max-height: 800px"
        />
        <span v-else>No Arrival Photo</span>
      </el-form-item>

      <!-- Departure Time -->
      <el-form-item label="Departure Time">
        <el-date-picker v-model="orderForm.departure_time" type="datetime" />
      </el-form-item>

      <!-- Departure Photo (圖片) -->
      <el-form-item label="Departure Photo">
        <img
          v-if="orderForm.departure_photo && orderForm.departure_photo.length"
          :src="orderForm.departure_photo[0]"
          style="max-width: 400px; max-height: 800px"
        />
        <span v-else>No Departure Photo</span>
      </el-form-item>

      <!-- Total Load Time -->
      <el-form-item label="Total Load Time">
        <el-input v-model.number="orderForm.total_load_time" type="number" />
      </el-form-item>

      <!-- Overtime -->
      <el-form-item label="Overtime">
        <el-input v-model.number="orderForm.over_time" type="number" />
      </el-form-item>

      <!-- Scale Ticket -->
      <el-form-item label="Scale Ticket">
        <el-input v-model="orderForm.scale_ticket" />
      </el-form-item>

      <!-- BoL (圖片) -->
      <el-form-item label="BoL">
        <img
          v-if="orderForm.bol_photo && orderForm.bol_photo.length"
          :src="orderForm.bol_photo[0]"
          style="max-width: 400px; max-height: 800px"
        />
        <span v-else>No BoL Photo</span>
      </el-form-item>

      <!-- Pick up TIR (圖片) -->
      <el-form-item label="Pick up TIR">
        <img
          v-if="orderForm.pickup_tir && orderForm.pickup_tir.length"
          :src="orderForm.pickup_tir[0]"
          style="max-width: 400px; max-height: 800px"
        />
        <span v-else>No Pickup TIR Photo</span>
      </el-form-item>

      <!-- Drop off TIR (圖片) -->
      <el-form-item label="Drop off TIR">
        <img
          v-if="orderForm.dropoff_tir && orderForm.dropoff_tir.length"
          :src="orderForm.dropoff_tir[0]"
          style="max-width: 400px; max-height: 800px"
        />
        <span v-else>No Drop off TIR Photo</span>
      </el-form-item>

      <!-- Void Out Ticket (圖片) -->
      <el-form-item label="Void Out Ticket">
        <img
          v-if="orderForm.void_out_ticket && orderForm.void_out_ticket.length"
          :src="orderForm.void_out_ticket[0]"
          style="max-width: 400px; max-height: 800px"
        />
        <span v-else>No Void Out Ticket Photo</span>
      </el-form-item>

      <!-- DOC CUT -->
      <el-form-item label="DOC CUT">
        <el-date-picker v-model="orderForm.doc_cut" type="datetime" />
      </el-form-item>
    </el-form>

    <template #footer>
      <el-button @click="closeDialog">Cancel</el-button>
      <el-button type="primary" @click="submitEdit">Save</el-button>
    </template>
  </el-dialog>
</template>

<script>
import { ref, watch, onMounted } from "vue";
import axios from "axios";
import { API_BASE_URL } from "@/config";

export default {
  props: {
    visible: Boolean,
    orderData: Object,
  },
  emits: ["update:visible", "update-success"],
  setup(props, { emit }) {
    const dialogVisible = ref(props.visible);

    // 使用本地變數 orderForm 而非直接修改 prop 的 orderData
    const orderForm = ref({});

    const customers = ref([]);
    const locations = ref([]);
    const piers = ref([]);
    const drivers = ref([]);

    const scaleHazmatOptions = ref([
      { label: "None", value: 0 },
      { label: "ScaleTicket", value: 1 },
      { label: "Hazmat", value: 2 },
      { label: "ScaleTicketAndHazmat", value: 3 },
    ]);

    const sslOptions = ref([
      { id: 1, name: "ACL" },
      { id: 2, name: "CMA/CGM" },
      { id: 3, name: "COSCO" },
      { id: 4, name: "ECLK" },
      { id: 5, name: "EVERGREEN" },
      { id: 6, name: "HAMBURG SUD" },
      { id: 7, name: "HAPAG" },
      { id: 8, name: "HMM" },
      { id: 9, name: "MAERSK" },
      { id: 10, name: "MSC" },
      { id: 11, name: "ONE" },
      { id: 12, name: "OOCL" },
      { id: 13, name: "SEALAND" },
      { id: 14, name: "TRKU" },
      { id: 15, name: "WANHAI" },
      { id: 16, name: "YANGMING" },
      { id: 17, name: "ZIM" },
      { id: 18, name: "To be decided" },
    ]);

    const loadStatusOptions = ref([
      { label: "Waiting For Vessel Date", value: 0 },
      { label: "Waiting For Driver To Confirm", value: 1 },
      { label: "Need Dispatch", value: 2 },
      { label: "Picking Up", value: 3 },
      { label: "On The Way To Loador/Unload", value: 4 },
      { label: "Loading Or Unloading", value: 5 },
      { label: "Dropping Off", value: 6 },
      { label: "Waiting For Validation", value: 7 },
      { label: "Cancelled", value: 8 },
      { label: "Dry Run", value: 9 },
      { label: "Complete", value: 10 },
      { label: "Needre Dispatch", value: 11 },
      { label: "Rejected Files", value: 12 },
      { label: "Void Out Ticket", value: 13 },
      { label: "Void Out Only", value: 14 },
    ]);

    const fetchDrivers = async () => {
      try {
        const token = localStorage.getItem("authToken");
        const response = await axios.post(
          `${API_BASE_URL}/Truck/TruckDriverList`,
          {
            Token: token,
            Timestamp: Date.now(),
            Sign: "string",
            truck_id: "",
          }
        );
        if (response.data.Code === 1000) {
          drivers.value = response.data.driver_list;
        }
      } catch (error) {
        console.error("Error fetching drivers:", error);
      }
    };

    const fetchCustomers = async () => {
      try {
        const token = localStorage.getItem("authToken");
        const response = await axios.post(`${API_BASE_URL}/Customer/List`, {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
        });
        if (response.data.Code === 1000) {
          customers.value = response.data.customer_list;
        }
      } catch (error) {
        console.error("Error fetching customers:", error);
      }
    };

    const fetchPiers = async () => {
      try {
        const token = localStorage.getItem("authToken");
        const response = await axios.post(`${API_BASE_URL}/Customer/Pier`, {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          pier_id: 0,
        });
        if (response.data.Code === 1000) {
          piers.value = response.data.pier_list;
        }
      } catch (error) {
        console.error("Error fetching piers:", error);
      }
    };

    const fetchCustomerLocations = async () => {
      if (!orderForm.value.customer_id) return;
      try {
        const token = localStorage.getItem("authToken");
        const response = await axios.post(`${API_BASE_URL}/Customer/Location`, {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          customer_id: orderForm.value.customer_id,
        });
        if (response.data.Code === 1000) {
          locations.value = response.data.location_list;
        }
      } catch (error) {
        console.error("Error fetching locations:", error);
      }
    };

    const updateCustomerAddress = () => {
      const selectedLocation = locations.value.find(
        (loc) => loc.location_id === orderForm.value.customer_location
      );
      orderForm.value.customer_address = selectedLocation
        ? selectedLocation.customer_address || ""
        : "";
    };

    const submitEdit = async () => {
      const token = localStorage.getItem("authToken");
      const response = await axios.post(`${API_BASE_URL}/Order/Edit`, {
        Token: token,
        Timestamp: Date.now(),
        Sign: "string",
        order_data: orderForm.value,
      });
      if (response.data.Code === 1000) {
        emit("update-success");
        closeDialog();
      } else {
        alert("Edit failed");
      }
    };

    const closeDialog = () => emit("update:visible", false);

    watch(
      () => props.visible,
      (val) => {
        dialogVisible.value = val;
        if (val && props.orderData) {
          // 在這邊加入 console.log 觀察 props.orderData 的內容
          console.log("props.orderData:", props.orderData);
          // 在開啟Dialog時複製資料到本地變數，避免直接修改prop
          orderForm.value = { ...props.orderData };
          fetchCustomers();
          fetchPiers();
          fetchCustomerLocations();
          fetchDrivers();
        }
      },
      { immediate: true }
    );

    onMounted(() => {
      fetchCustomers();
      fetchPiers();
      fetchDrivers(); // <-- 新增這一行
    });

    return {
      dialogVisible,
      orderForm,
      customers,
      locations,
      piers,
      drivers,
      scaleHazmatOptions,
      loadStatusOptions,
      sslOptions,
      fetchCustomerLocations,
      updateCustomerAddress,
      submitEdit,
      closeDialog,
    };
  },
};
</script>
